<template>
  <div class="main rule-group">
    <div class="main-table">
      <div style="border:1px solid #ccc;">
        <div class="table-msg">
          <div class="msg-left">
            <p>{{dateline}}</p>
          </div>
          <div class="msg-center">
            <p>说明：所有用于撞库数据需当天下载，当天上传，否则数据失效，按钮生效时间为每天01:00-23:00，当日23:00至次日1:00按钮不可用</p>
          </div>
          <div class="msg-right">
            <!--<el-button type="button" class="btn-small" @click="addproj">添加品牌</el-button>-->
            <el-button class="btn-small" v-show="del.allState" @click="batchRemove">批量删除</el-button>
            <el-button class="btn-small" v-show="del.state" @click="cancel">取消</el-button>
            <el-button class="btn-small" style="background-color: #007bc4;color: #fff;" v-show="del.state" @click="delArr">删除</el-button>
          </div>
        </div>
        <div class="table_container">
          <div>
            <ul>
              <li style="width:5%;height:20px;" v-show="del.state"></li>
              <li style="width:8%">序号</li>
              <li style="width:10%">品牌</li>
              <li style="width:26%">可下载文件名</li>
              <li style="width:13%">数据条数</li>
              <li style="width:20%">最近更新时间</li>
              <li style="width:18%"></li>
            </ul>
            <ul>
              <li style="width:24%;">已上传文件名</li>
              <li style="width:14%">上传状态</li>
              <li style="width:14%">数据条数</li>
              <li style="width:30%">最近更新时间</li>
              <li style="width:17%;"></li>
            </ul>
          </div>
          <div class="table_container">
            <div v-for="(item,index) in list.data">
              <ul>
                <li style="width:5%" v-show="del.state">
                  <el-checkbox @change="checked=>changedel(checked,item.id,item)" v-model='item.checked' name="list"></el-checkbox>
                </li>
                <li style="width:8%">{{index*1+1}}</li>
                <li style="width:10%">{{item.name}}</li>
                <li style="width:26%">{{item.name}}_{{dateformat}}</li>
                <li style="width:13%">{{item.number}}</li>
                <li style="width:20%">{{dateline}}</li>
                <li style="width:18%">
                  <el-button type="button" class="btn-small" :disabled="!btn" @click="download(item.id, item.name+'_'+dateformat)">下载</el-button>
                </li>
              </ul>
              <ul>
                <li style="width:24%">{{item.filename_name}}</li>
                <li style="width:14%"><span v-if='item.status== 0'>上传中</span><span v-else-if='item.status== 1'>已上传</span></li>
                <li style="width:14%">{{item.num}}</li>
                <li style="width:30%">{{item.update_time}}</li>
                <li style="width:17%">
                  <el-upload
                    class="upload-demo"
                    ref="upload"
                    :action="uploadUrl(item.id)"
                    :show-file-list="false"
                    :file-list="fileList"
                    :auto-upload="true"
                    :data='fileform'
                    :before-upload="beforeAvatarUpload"
                    :on-success="uploadSuccess">
                    <!--//:on-error="uploadFalse" // :on-success="uploadSuccess"-->
                    <el-button @click="prjidIds(item.id)"  type="button" :disabled="!btn" class="btn-small">导入文件</el-button>
                  </el-upload>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <page-table show_size="true" v-bind:cur="page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <!-- <el-dialog :visible.sync="dialog_add" title="添加品牌">
      <template v-for="item in proj_list">
        <el-select class="dialog_projselect" style="width:70%">
          <template>
            <el-option value="">VIPKID</el-option>
          </template>
        </el-select>
      </template>
      <el-button @click="addprojselect">添加品牌</el-button>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog_add=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog> -->
    <el-dialog :visible.sync="dialog_delete" width="35%" title="删除确认">
      <p style="text-align:center">是否确认删除<span v-for='(item, index) in delList'>{{item.name}}<em v-if='index!=delList.length-1'>、</em></span>?</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog_delete=false">取消</el-button>
        <el-button type="primary" @click="save">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import pageTable from '@/components/pager'
import {format} from '@/config/util'
import fileDownload from 'js-file-download'
import ajax from '@/config/ajax'
import Qs from 'qs'

export default {
  name:"brand-collision",
  components:{pageTable},
  data () {
    return {
      dateline: format(new Date(), "yyyy-MM-dd"),  //当前日期
      dateformat: format(new Date(), "yyyyMMdd"),
      list: {
        data: [],            //数据集
        totalPage: 0,        //总页数
        totalCount: 0        //总条数
      },
      fileList: [],         //上传文件
      filedata: [],
      fileform: {
        prjid_local: ''
      },
      dialog_add: false,      //添加品牌弹框
      proj_num: 1,          //添加品牌数量
      proj_list: [{
        value: 1
      }],         //添加品牌数组
      delList: [],
      dialog_delete:false,
      del:{               //批量删除相关参数
        state:false,
        allState:true
      },
      deleteIds: [],         //批量删除选中的数据
      btn:true,
      size: 20,
      page: 1,
    }
  },
  created() {
    this.getInitialList();
    for(let i=0;i<this.list.data.length;i++){
      this.getupload(this.list.data[i].id,i);
    }
    //判断时间范围
    let date = new Date();
    let dates = date.getFullYear()+"-"+(date.getMonth()*1+1)+"-"+date.getDate();
    let startime = dates+" "+"23:00";
    let endtime = date.getFullYear()+"-"+(date.getMonth()*1+1)+"-"+(date.getDate()*1+1)+" "+"1:00";
    if(new Date(startime) >= date >= new Date(endtime)){
      this.btn = false;
    }else{
      this.btn = true;
    }
  },
  methods: {
    //初始化列表
    getInitialList() {
      // this.deleteIds = "";
      this.delList = [];
      var param = {
        size: this.size,
        page: this.page
      }
      return ajax.request({
        method: 'get',
        url: 'hit/all',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: param
      }).then(res => {
        // console.log(res.data)
        for(let i=0;i<res.data.list.length;i++){
          this.$set(res.data.list[i],'checked',false);
        }
        this.list.data = res.data.list;
        this.list.totalCount = res.data.totalCount;
        this.list.totalPage = res.data.totalPage;
        
      }).catch(error => {
        console.log(error)
      })
    },
    //下载文件
    download(prjid_local,filename) {
      var params = {
        prjid_local: prjid_local
      }
      return ajax.request({
        method: 'post',
        url: "/hit/download",
        responseType: 'arraybuffer',
        transformRequest: [function (params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data: params
      }).then((res) => {
        let fileDownload = require("js-file-download");
        if(prjid_local == '30'){
          fileDownload(res,"下载"+filename+".csv");
        }else{
          fileDownload(res,"下载"+filename+".xlsx");
        }
      }).catch((err) => {
        console.log(err);
      })
    },
    uploadUrl(id){
      return "http://39.100.61.222/hit/upload?prjid_local="+id+"&file=";
    },
    uploadSuccess(response, file, fileList) {
      // console.log(response);
      if (response.code == '200') {
        this.$message.success("导入成功")
      } else {
        this.$message.success("导入失败")
      }
    },
    //上传文件之前调用的函数
    beforeAvatarUpload(file) {
      const extension = file.name.split(".")[1] === "xlsx";
      const extension2 = file.name.split(".")[1] === "csv";
      const isLt2M = file.size / 1024 / 1024 < 10;
      if (!extension && !extension2) {
        alert("上传模板只能是 xlsx、csv 格式!");
      }
      // if (!isLt2M) {
      //   console.log("上传模板大小不能超过 10MB!");
      // }
      return extension || extension2;
    },
    prjidIds(id){
      this.fileform.prjid_local = id;
    },
    addproj() {
      this.dialog_add = true;
    },
    addprojselect() {    // 弹框添加品牌
      var data = {
        value: 1
      };
      this.proj_list.push(data);
    },
    save() {
      return ajax.request({
        method: 'post',
        url: 'hit/delete',
        data:{
          ids: this.deleteIds.join(",")
        }
      }).then(res => {
        if(res.code ==  '200'){
          for(var i in this.delList){
            var val = this.list.data.indexOf(this.delList[i]);
            if(val > -1){
              this.list.data.splice(val,1);
            }
          }
          this.deletetype = false;
          this.dialog_delete = false;
        }
      }).catch(err => {
        console.log(err);
      })
    },
    batchRemove(){
      this.del.state=true
      this.del.allState=false
    },
    cancel(){
      this.del.state=false
      this.del.allState=true
    },
    //删除
    delArr() {
      if (this.delList.length <=0){
        this.$message.info("请选择您要删除的文件");
      }else{
        this.dialog_delete = true;
      }
    },
    changedel(checked, id, data) {
      if(checked){
        this.deleteIds.push(id);
        this.delList.push(data);
      }else{
        var ids = this.deleteIds.indexOf(id);
        var datas = this.delList.indexOf(data);
        if(ids > -1){
          this.deleteIds.splice(ids,1);
        }
        if(datas > -1){
          this.delList.splice(datas,1);
        }
      }
    },
    change_data (index, size) {
      this.list.page = index;
      this.list.size = size;
      this.getInitialList()
    },
  }
}
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';

  .main-table {
    padding: 15px;
  }

  .main-table .table-msg {
    border-bottom: 1px solid #ccc;
    margin-bottom: 0;
  }

  .main-table .table-msg .msg-left {
    width: 25%;

  }

  .main-table .table-msg .msg-left p {
    line-height: 60px;
    color: #000;
    padding: 10px;
  }

  .main-table .table-msg .msg-center {
    float: left;
    width: 50%;
    text-align: center;
  }

  .main-table .table-msg .msg-center p {
    line-height: 25px;
  }

  .main-table .table-msg .msg-right {
    width: 23%;
    padding: 25px 10px;
  }

  .main .dialog_projselect {
    margin-bottom: 16px;
  }

  .main-table .table_container div {
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid #ccc;
  }

  .main-table .table_container div:last-child {
    border-bottom: none;
  }

  .main-table .table_container div ul {
    float: left;
    height: 40px;
    line-height: 40px;
  }

  .main-table .table_container div ul:first-child {
    width: 48%;
  }

  .main-table .table_container div ul:nth-child(2) {
    width: 52%;
  }

  .main-table .table_container div ul li {
    float: left;
    text-align: center;
    height:30px;
  }

  .el-select-dropdown__item {
    padding: 0 20px;
  }
</style>



